import {Component} from '@angular/core';
import {IonicPage, NavController, ModalController, NavParams, AlertController, ToastController, LoadingController} from 'ionic-angular';
import {RequestService} from "../../providers/request.service";
import {UserService} from "../../providers/user.service";
import {Profil} from "../../providers/model/profil";
import {ArticleService} from "../../providers/article.service";
import { InAppBrowser } from '@ionic-native/in-app-browser';
import {TransactionWeb3Service} from "../../providers/transaction..web3.service";

@IonicPage()
@Component({
  selector: 'page-article',
  templateUrl: 'article.html',
})
export class ArticlePage {

  article: any;
  key: any;
  mine: any;
  isInventory: any;
  profil: Profil;
  uid: string;
  alive: boolean = true;

  constructor(private params: NavParams,
              private navCtrl: NavController,
              public loadingCtrl: LoadingController,
              private requestService: RequestService,
              public modalCtrl: ModalController,
              public userService: UserService,
              public alertCtrl: AlertController,
              private articleService: ArticleService,
              public toastCtrl: ToastController,
              private iab: InAppBrowser,
              private transactionWeb3Service: TransactionWeb3Service
  ) {
    this.article = params.get('article');
    this.key = params.get('key');
    this.mine = params.get('mine');
    if(!this.mine) {
      this.mine = false;
    }
    this.isInventory = params.get('isInventory');
    this.uid = this.userService._userFirebase.uid;
  }

  ionViewDidLoad() {
    this.userService.getProfil(this.article.uid).takeWhile(() => this.alive).subscribe(p => {
      this.profil = p;
      this.profil['rate'] = 0;
    });
  }

  ionViewWillEnter() {
    this.alive = true;
  }

  ionViewWillLeave() {
    this.alive = false;
  }

  generatePayment() {
    //TODO choose crypto trigram
    if(!this.isInventory) {
      let createdCode = this.requestService.generateRequest(
        "pay-article",
        { id: this.key
        });

      let modal = this.modalCtrl.create('ModalArticleQrcode', {
        createdCode: createdCode
      });
      modal.onDidDismiss(data => {
        if(data == "success") {
          this.navCtrl.pop();
        }
      });
      modal.present();
    } else {
      let createdCode = this.requestService.generateRequest(
        "pay-inventory",
        { uid: this.userService._userFirebase.uid, id: this.article.ean
        });

      let modal = this.modalCtrl.create('ModalArticleQrcode', {
        createdCode: createdCode
      });
      modal.onDidDismiss(data => {
        if(data == "success") {
          this.navCtrl.pop();
        }
      });
      modal.present();
    }
  }

  buy() {
    let createdCode = this.requestService.generateRequest(
      "pay-article",
      { id: this.key
      });

    if(createdCode.indexOf('neoplace') >= 0) {
      this.requestService.processRequest(createdCode);
    }
  }

  contact() {
    let uidArray = [this.userService._userFirebase.uid, this.article.uid];
    uidArray = uidArray.sort();

    this.userService.getProfil(this.article.uid).takeWhile(() => this.alive).subscribe(profil => {
      if(profil.telegram) {
        let confirm = this.alertCtrl.create({
          title: 'Contact',
          message: 'Chat with Telegram or NeoPlace chat ?',
          cssClass: 'confirm-alert',
          buttons: [
            {
              text: 'Telegram',
              handler: () => {
                const browser = this.iab.create('https://t.me/' + profil.telegram, '_system');
              }
            },
            {
              text: 'NeoPlace chat',
              handler: () => {
                this.navCtrl.push('MessagesPage', {id: uidArray[0] + "-" + uidArray[1], profil: profil, otherUid: this.article.uid});
              }
            }
          ]
        });
        confirm.present();
      } else {
        this.navCtrl.push('MessagesPage', {id: uidArray[0] + "-" + uidArray[1], profil: profil, otherUid: this.article.uid});
      }
    });
  }

  contactBuyer() {
    let uidArray = [this.article.buyeruid, this.article.uid];
    uidArray = uidArray.sort();
    this.userService.getProfil(this.article.buyeruid).takeWhile(() => this.alive).subscribe(profil => {
      this.navCtrl.push('MessagesPage', {id: uidArray[0] + "-" + uidArray[1], profil: profil, otherUid: this.article.uid});
    });

    this.userService.getProfil(this.article.buyeruid).takeWhile(() => this.alive).subscribe(profil => {
      if(profil.telegram) {
        let confirm = this.alertCtrl.create({
          title: 'Contact',
          message: 'Chat with Telegram or NeoPlace chat ?',
          cssClass: 'confirm-alert',
          buttons: [
            {
              text: 'Telegram',
              handler: () => {
                const browser = this.iab.create('https://t.me/' + profil.telegram, '_system');
              }
            },
            {
              text: 'NeoPlace chat',
              handler: () => {
                this.navCtrl.push('MessagesPage', {id: uidArray[0] + "-" + uidArray[1], profil: profil, otherUid: this.article.buyeruid});
              }
            }
          ]
        });
        confirm.present();
      } else {
        this.navCtrl.push('MessagesPage', {id: uidArray[0] + "-" + uidArray[1], profil: profil, otherUid: this.article.buyeruid});
      }
    });
  }

  confirmReception() {
    let confirm = this.alertCtrl.create({
      title: 'Confirmation',
      message: 'Are you sure to confirm reception ? \n Funds will be unlocked',
      cssClass: 'confirm-alert',
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
            //console.log('Disagree clicked');
          }
        },
        {
          text: 'Confirm',
          handler: () => {
            const loading = this.loadingCtrl.create({
              content: 'Please wait while unlocking funds'
            });

            loading.present();

            this.transactionWeb3Service.unlockFunds(this.key).subscribe(value => {
              loading.dismissAll();
              this.article.validate = 'true';
              this.articleService.updateArticle(this.key, this.article);
              let toast = this.toastCtrl.create({
                message: 'Item receipt saved in Smart contract.\nFunds have been sent to seller\nTx: ' + value.tx,
                duration: 4000,
                position: 'bottom'
              });
              toast.present(toast);
            });

          }
        }
      ]
    });
    confirm.present();
  }

  delete() {
    let confirm = this.alertCtrl.create({
      title: 'Delete',
      message: 'Are you sure to delete your item ?',
      cssClass: 'confirm-alert',
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
            //console.log('Disagree clicked');
          }
        },
        {
          text: 'Confirm',
          handler: () => {
            if(!this.isInventory) {
              this.articleService.deleteArticle(this.key);
            } else {
              this.articleService.deleteInventory(this.key);
            }

            this.toastCtrl.create({
              message: 'Item deleted',
              duration: 2000,
              position: 'bottom'
            }).present();

            this.navCtrl.pop();
          }
        }
      ]
    });
    confirm.present();
  }

  soon() {
    this.toastCtrl.create({
      message: 'Coming soon',
      duration: 2000,
      position: 'bottom'
    }).present();
  }


}



